header {
  height: 216px;
  max-width: 320px;
  background-image: url("mobile-home-bg.e50ec283.jpg");
  background-repeat: no-repeat;
  background-size: 100% 216px;
  margin: 0 auto;
}

@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 72dpi), (min-resolution: 2dppx) {
