    background-image: url("2x-mobile-home-bg.f55da79c.jpg");
  }
}

.header-logo {
  align-items: center;
  gap: 5px;
  display: flex;
}

.header-logo p {
  margin: 0;
  font-size: 30px;
  font-weight: 500;
  display: none;
}

.header-links {
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  font-size: 12px;
  display: flex;
}

.header-links a {
  color: inherit;
  text-decoration: none;
}

