  justify-content: space-between;
  padding-top: 40px;
  padding-bottom: 55px;
  display: flex;
}

.header-form {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.search-form {
  width: 280px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.search-form:after {
  content: "";
  height: .5px;
  width: 100%;
  background: #fff;
  position: absolute;
  bottom: -4px;
  left: 0;
}

.search-form input {
  width: 265px;
  color: inherit;
  background-color: #0000;
  border: none;
  font-size: 14px;
  font-weight: 400;
}

.search-form input::placeholder {
  color: #fff;
}

.search-form button {
  background-color: #0000;
  border: none;
  padding: 1px 0;
}

.errorDisplay {
  color: #ff001b;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 400;
}

#home {
  position: relative;
}

#home:after {
  content: "";
  height: 3px;
  width: 100%;
  background: #ff001b;
  position: absolute;
  bottom: -3px;
  left: 0;
}

